// 
export interface Emotion {
  id: string;
  name: string;
  icon: string;
  color: string;
  value: number; // 1-10
}

// 
export interface EmotionRecord {
  id: string;
  date: string; // YYYY-MM-DD
  emotion: Emotion;
  intensity: number; // 1-5
  diary: string;
  photos?: string[]; // base64
  audio?: string; // base64
  createdAt: string; // ISO
  updatedAt: string;
}

// 
export interface WordCloudData {
  text: string;
  value: number;
  color?: string;
}

// 
export interface TrendData {
  date: string;
  emotionValue: number;
  emotionName: string;
}

// 
export interface EmotionStats {
  emotion: string;
  count: number;
  percentage: number;
  color: string;
}

// 
export interface AppState {
  records: EmotionRecord[];
  selectedDate: string;
  currentRecord?: EmotionRecord;
  isLoading: boolean;
  error?: string;
}

